







































































import {
  Vue,
  Component,
  Prop,
  Mixins,
  Inject,
  Watch
} from "vue-property-decorator";
import FileBrowser from "./FileBrowser.vue";
import path from "path-browserify";
import { createDirectory } from "../../store/api/files";

/**
 * v-model controls open state
 */
@Component({
  components: {
    FileBrowser
  }
})
export default class FolderSelectorDialog extends Mixins() {
  @Prop({
    type: [Boolean],
    required: true
  })
  value!: boolean;

  @Prop({
    type: [String],
    default: ""
  })
  initialLocation!: string;

  @Prop({
    type: [String, Boolean],
    default: false
  })
  clientpath!: string | false;

  @Prop({
    type: [String, Boolean],
    default: false
  })
  client!: string | false;
  createFolderPrompt = false;

  @Watch("isOpen")
  async onOpen() {
    if (this.isOpen) {
      await (this.$refs.fileBrowser as FileBrowser).manuallySetFolders([]);
      if (this.initialLocation) {
        this.onInitialLocationChange();
      }
    }
  }

  @Watch("initialLocation", { immediate: true })
  async onInitialLocationChange() {
    console.log("locationchange", this.initialLocation);
    if (this.initialLocation) {
      this.chosenLocations = [this.initialLocation];
      if (this.$refs.fileBrowser) {
        await (this.$refs.fileBrowser as FileBrowser).manuallySetFolders([
          this.initialLocation
        ]);
        await (this.$refs.fileBrowser as FileBrowser).openFolder(
          this.initialLocation
        );
        this.chosenLocations = [this.initialLocation];
      }
    } else {
      this.chosenLocations = [];
    }
  }

  newFolderName = "";

  get canCreateFolder() {
    return Boolean(this.newFolderName);
  }

  async createFolder() {
    if (!this.chosenLocation) {
      return;
    }
    const newPath = path.join(this.chosenLocation, this.newFolderName);
    await createDirectory({ path: newPath });
    await this.refreshBrowser();
    this.newFolderName = "";
  }

  get isOpen() {
    return this.value;
  }

  set isOpen(val) {
    this.$emit("input", val);
  }

  chosenLocations: string[] = [];

  get chosenLocation() {
    return this.chosenLocations[0] || false;
  }

  async refreshBrowser() {
    if (this.$refs.fileBrowser) {
      await (this.$refs.fileBrowser as FileBrowser).refreshBrowser();
      await (this.$refs.fileBrowser as FileBrowser).manuallySetFolders([
        this.chosenLocation || this.initialLocation
      ]);
      await (this.$refs.fileBrowser as FileBrowser).openFolder(
        this.chosenLocation || this.initialLocation
      );
    }
  }
}
